	export default {
        created(){
           this.getGoods()
        },
		data() {
			return {
                    viplist:['VIP','SVIP','SSVIP'],
                    currentIndex:0,
                    goods:[

                    ]
			};
		},
        methods:{
            // 获取vip列表
            async getGoods(){
                await this.$u.post('/get_privileged_goods').then(res=>{
                    this.goods = res.data.map(o=>{return{
                        '特性等级':o.level, 
                        '提现手续费':o.out+'元',
                        '发布悬赏任务技术服务费':o.push_task+'元',
                        '发布抖音任务技术服务费':o.push_dy_task+'元',
                        '开通时长':o.term+'天',
                        '开通价格':o.price+'元',
                        }});
                    });
               
                
            },
            // 购买vip
            async buy(){
                  await this.$u.post('/buy_privileged_goods',{
                      type:this.currentIndex+1
                  }).then(res=>{
                         uni.showToast({
                             title:"购买成功",
                         })
                         setTimeout(()=>{
                             uni.navigateBack({
                                 delta:1
                             })
                         },2000)
                        
                  })
             }
        },
  
	}
